.walletPage {
    display: flex;
    justify-content: center;
    height: 100vh;
}

.walletCard {
    background-color: white;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
    border-radius: 10px;
    margin-top: 70px;
    height: calc(100vh - 150px);
    width: 93vw;
    max-width: 540px;
}

.infoBlock {
    height: 160px;
    width: 93vw;
    max-width: 540px;
    padding-top: 10px;
    border-bottom-style: solid;
    border-bottom-width: 1px;
}

.balanceLabel {
    margin-top: 10px;
    margin-left: 160px;
}

.balance {
    margin-left: 160px;
    margin-top: 10px;
}

.address {
    display: inline-block;
    margin-left: 10px;
}

.addressLabel {
    margin-left: 163px;
    margin-top: 12px;
}

h2 {
    margin: 0;
}

h3 {
    margin: 0;
}

.qrCode {
    padding-left: 7px;
    width: 145px;
    height: 145px;
    float: left;
}

.txHistory {
    height: calc(100% - 182px);
    width: 100%;
    overflow-y: scroll;
    overflow-x: hidden;
}

.clipboardIcon {
    width: 20px;
    height: 20px;
    margin-left: 10px;
}

.clipboardIcon:hover {
    cursor: pointer;
}

.clipboardAlert {
    background: rgba(0, 0, 0, 0.8);
    color: #fff;
    display: inline-block;
    padding: 5px;
    border-radius: 5px;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    height: 2.5rem;
    max-width: 200px;
    line-height: 2.5rem;
    margin: auto;
    text-align: center;
    z-index: 1000;
    display: none; 
}

.clipboardAlertShow {
    background: rgba(0, 0, 0, 0.8);
    color: #fff;
    display: inline-block;
    padding: 5px;
    border-radius: 5px;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 90%;
    height: 2.5rem;
    max-width: 200px;
    line-height: 2.5rem;
    margin: auto;
    text-align: center;
    z-index: 1000;
    display: block; 
}

.clip {
    float: right;
    margin-top: 4px;
    margin-right: 80px;
}

#addressInput {
    position: fixed;
    top: -100px;
}

@media only screen and (max-width: 600px) {
    .qrCode {
        padding-left: 7px;
        width: 120px;
        height: 120px;
        float: left;
    }
    .address {
        display: inline-block;
        margin-left: 10px;
        margin-top: 4px;
        font-size: 18px;
    }
    .addressLabel {
        margin-left: 135px;
        font-size: 20px;
    }
    .txHistory {
       height: calc(100% - 140px);
    }
    .clip {
        float: right;
        margin-top: 4px;
        margin-right: 15px;
    }
    .infoBlock {
        height: 130px;
        width: 93vw;
        max-width: 540px;
        padding-top: 10px;
        border-bottom-style: solid;
        border-bottom-width: 1px;
    }
    .balance {
        margin-left: 136px;
        margin-top: 10px;
        font-size: 20px;
    }
}